"use client";

import Link from "next/link";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { AuthShell } from "@/components/auth/auth-shell";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

const emailSchema = z.object({
  email: z.email("Informe um e-mail válido"),
});

const resetSchema = z
  .object({
    token: z.string().min(6, "Token inválido"),
    password: z.string().min(8, "Senha deve ter no mínimo 8 caracteres"),
    confirmPassword: z.string().min(8, "Confirmação inválida"),
  })
  .refine((values) => values.password === values.confirmPassword, {
    error: "As senhas não conferem",
    path: ["confirmPassword"],
  });

type EmailInput = z.infer<typeof emailSchema>;
type ResetInput = z.infer<typeof resetSchema>;

export default function ForgotPasswordPage() {
  const [step, setStep] = useState<1 | 2 | 3>(1);

  const emailForm = useForm<EmailInput>({
    resolver: zodResolver(emailSchema),
    defaultValues: { email: "" },
  });

  const resetForm = useForm<ResetInput>({
    resolver: zodResolver(resetSchema),
    defaultValues: {
      token: "",
      password: "",
      confirmPassword: "",
    },
  });

  const sendRecovery = async () => {
    await new Promise((resolve) => setTimeout(resolve, 400));
    setStep(2);
  };

  const confirmReset = async () => {
    await new Promise((resolve) => setTimeout(resolve, 400));
    setStep(3);
  };

  return (
    <AuthShell
      title="Recuperar senha"
      subtitle="Fluxo em etapas para solicitação, validação de token e redefinição"
    >
      <div className="mb-4 text-xs text-muted-foreground">AUTH-04 • Etapa {step} de 3</div>

      {step === 1 ? (
        <form className="space-y-4" onSubmit={emailForm.handleSubmit(sendRecovery)} noValidate>
          <div className="space-y-2">
            <Label htmlFor="email">E-mail da conta</Label>
            <Input id="email" type="email" placeholder="voce@dominio.com" {...emailForm.register("email")} />
            {emailForm.formState.errors.email ? (
              <p className="text-sm text-destructive">{emailForm.formState.errors.email.message}</p>
            ) : null}
          </div>
          <Button type="submit" className="w-full" disabled={emailForm.formState.isSubmitting}>
            {emailForm.formState.isSubmitting ? "Enviando..." : "Enviar recuperação"}
          </Button>
        </form>
      ) : null}

      {step === 2 ? (
        <form className="space-y-4" onSubmit={resetForm.handleSubmit(confirmReset)} noValidate>
          <div className="space-y-2">
            <Label htmlFor="token">Token recebido por e-mail</Label>
            <Input id="token" placeholder="Ex: 123456" {...resetForm.register("token")} />
            {resetForm.formState.errors.token ? (
              <p className="text-sm text-destructive">{resetForm.formState.errors.token.message}</p>
            ) : null}
          </div>

          <div className="space-y-2">
            <Label htmlFor="password">Nova senha</Label>
            <Input id="password" type="password" placeholder="********" {...resetForm.register("password")} />
            {resetForm.formState.errors.password ? (
              <p className="text-sm text-destructive">{resetForm.formState.errors.password.message}</p>
            ) : null}
          </div>

          <div className="space-y-2">
            <Label htmlFor="confirmPassword">Confirmar nova senha</Label>
            <Input
              id="confirmPassword"
              type="password"
              placeholder="********"
              {...resetForm.register("confirmPassword")}
            />
            {resetForm.formState.errors.confirmPassword ? (
              <p className="text-sm text-destructive">{resetForm.formState.errors.confirmPassword.message}</p>
            ) : null}
          </div>

          <div className="flex gap-2">
            <Button type="button" variant="outline" className="flex-1" onClick={() => setStep(1)}>
              Voltar
            </Button>
            <Button type="submit" className="flex-1" disabled={resetForm.formState.isSubmitting}>
              {resetForm.formState.isSubmitting ? "Confirmando..." : "Confirmar nova senha"}
            </Button>
          </div>
        </form>
      ) : null}

      {step === 3 ? (
        <div className="space-y-3">
          <p className="text-sm text-muted-foreground">Senha redefinida com sucesso.</p>
          <Link href="/login">
            <Button className="w-full">Ir para login</Button>
          </Link>
        </div>
      ) : null}
    </AuthShell>
  );
}
